import React, { Component } from "react";
import axios from "./axios";

class Gravatar extends Component {
  state = {
    data: [],
  };
  //const emailAddress ="rafiul.alam@yahoo.com"
  componentDidMount() {
    const avatarMail = "bm_numan@yahoo.com";
    axios.get("https://en.gravatar.com/" + avatarMail + ".json").then((res) => {
      this.setState({ data: res.data });
    });
  }

  render() {
    if (!this.state) return <p className="loading"></p>;
    // I get data in console

    const data = this.state.data;
    const entry = data.entry;
    console.log(entry);
    return (
      <div style={{ marginTop: 60, textAlign: "center" }}>
        {entry &&
          entry.map((item) => (
            <div key={item.id}>
              <img
                src={item.thumbnailUrl}
                className="rounded-circle"
                alt=""
                style={{ marginBottom: "20px" }}
              />
              <h6>
                {item.name === undefined || item.name.length === 0
                  ? item.displayName
                  : item.name.formatted}
              </h6>
            </div>
          ))}
      </div>
    );
  }
}
export default Gravatar;
