import React, { Component, Fragment } from "react";

class DownloadToday extends Component {
  state = {
    local: [],
  };
  componentDidMount() {
    setTimeout(() => {
      this.setState({
        dToday: localStorage.getItem("downloadToday"),
        dYesterday: localStorage.getItem("downloadYesterday"),
      }); //set local state
    }, 5000);
  }
  render() {
    const downloadToday = this.state.dToday;
    const range = this.state.dToday - this.state.dYesterday;
    return (
      <Fragment>
        {downloadToday}
        <div style={{ fontSize: 16 }}>
          <span
            className={
              range >= 0 ? "badge badge-success" : "badge badge-danger"
            }
          >
            {range} {""}
            vs yesterday
          </span>
        </div>
      </Fragment>
    );
  }
}
export default DownloadToday;
