import React, { Component, Fragment } from "react";
import DownloadToday from "./downloadToday";

class DownloadTodayView extends Component {
  state = {
    local: [],
  };
  componentDidMount() {
    setTimeout(() => {
      this.setState({
        dToday: localStorage.getItem("downloadToday"),
        dYesterday: localStorage.getItem("downloadYesterday"),
      }); //set local state
    }, 5000);
  }
  render() {
    const downloadToday = this.state.dToday;
    const range = this.state.dToday - this.state.dYesterday;
    return (
      <Fragment>
        <DownloadToday downloadToday={downloadToday} rang={range} />
      </Fragment>
    );
  }
}
export default DownloadTodayView;
